}

::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ffffff !important;
  /* opacity: 1; Firefox */
}

::-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ffffff !important;
  /* opacity: 1; Firefox */
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #ffffff !important;
  /* opacity: 1; Firefox */
}

.newnotediv {
  margin-top: 2%;
  margin-bottom: 2%;
  overflow-y: hidden;
}

.addnotebtn {
  margin-top: 2%;
  background-color: #5bcd04 !important;
}

.contentnote {
  white-space: pre-wrap;
  /* height: 20vh; */
}

.notetile {
  cursor: pointer;
  background-color: #212121 !important;
  border-style: solid !important;
  border-color: #5bcd04 !important;
  border-width: 1px;
}

.editnoteheader {
  border-bottom: #212121 !important;
}

.editnotefooter {
  border-top: #212121 !important;
}

.inputbordercolor {
  border-color: #5bcd04 !important;
}

.notetitlechangeinput {
  width: 95% !important;
}

.loadingbar {
  background-image: linear-gradient(
    to right,
    #5bcd04 30%,
    #ededed 30%
  ) !important;
}
.timeinmodal {
  font-size: 0.8rem;
  /* padding-top: 10px;
  padding-right: 10px; */
}

.deletenotebutton {
  float: right !important;
}

.cardnotefooter {
  justify-content: space-between !important;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #777474;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(51, 50, 50);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(44, 43, 43);
}

@media screen and (max-width: 754px) {
  .dashboardbtngroup {
    display: flex !important;
    flex-direction: column;

    justify-content: space-around !important;
    margin-bottom: 1%;
    margin-top: 10%;
    /* height: 10px */
  }
  .spinnerdashboard {
    padding: 9px;
    text-align: center;
  }
}

